
$('.js-agree').click(function (event) {
          event.preventDefault();

          if (!confirm('Are you sure you want to agree() this post?')) {
            return;
          }

          //child
          var span = $(this).children('span:first-child');
          // var span = $(this).children().first();

          $.ajax({
            type:'GET',
            dataType:'text',
            url:$(this).attr('href'),
            success:function(results) {
                span.text(results);
            }
          });
});

